import { createRandomOffers } from '../mock/offer-mock.js';

export default class OffersModel {
  #offers = null;
  constructor() {
    this.#offers = createRandomOffers();
  }

  get offers() {
    return this.#offers;
  }

  getCurrentOffer(type) {
    if (this.#offers) {
      return this.#offers.find((item) => item.type === type);
    }
  }
}
