import { destinationCreateAll } from '../mock/destination.js';

export default class DestinationsModel {
  #destinationAll = null;
  constructor() {
    this.#destinationAll = destinationCreateAll();
  }

  get destinationAll() {
    return this.#destinationAll;
  }

  getDestinationId(id) {
    return this.destinationAll.find((item) => item.id === id);
  }
}
